import React, { Suspense, useState } from 'react';
import { Canvas, useFrame, useThree } from 'react-three-fiber';
import { OrbitControls, CameraShake } from "@react-three/drei";
import * as THREE from 'three'

import { KeyLight, FillLight, RimLight } from './components/lights';
import { GameManager } from './components/GameManager'
import { Balls } from './Ball'
import { Gauge2 } from './components/Gauge2'
import { AppManager } from './components/AppManager'
import { ScorePrompt } from './components/ScorePrompt'
import { RFirePrompt } from './components/RFirePrompt'
import Scene from './components/Scene'
import Mascots from './components/Mascots'
import Mascots2 from './components/Mascots2'
import Mascots3 from './components/Mascots3'
import Hoop from './components/Hoop'
import Cblock from './components/ChampBlock'

import "./index.css";

function Rig() {
  const [vec] = useState(() => new THREE.Vector3())
  const { camera } = useThree()
  useFrame(() => camera.position.lerp(vec.set( 0, 5, 6), 0.05))
  return <CameraShake maxYaw={0.01} maxPitch={0.01} maxRoll={0.01} yawFrequency={0.1} pitchFrequency={0.1} rollFrequency={0.1} />
}

export default function App() {
  return (
    <>
      <AppManager />
      <ScorePrompt />
      <RFirePrompt />
      <Gauge2 />
      <Canvas camera={{ position: [0,5,5] }} onCreated={({ camera }) => {camera.lookAt(0,0.61,-15)}}>
        {/* <OrbitControls /> */}
        <GameManager />
        <ambientLight intensity={1} />
        <KeyLight brightness={5} color="#ffbdf4" />
        <FillLight brightness={250} color="#bdefff" />
        {/* <FillLight brightness={2.6} color="#bdefff" /> */}
        {/* <RimLight brightness={54} color="#fff" /> */}
        <Suspense fallback={null}>
          <Balls />
          <Scene scale={[.12,.12,.12]} rotation={[0,Math.PI,0]}  />
          {/* <Mascots scale={[.1,.1,.1]} rotation={[0,Math.PI,0]}/> */}
          {/* <Mascots2 scale={[8,8,8]} position={[0,0,-4]}/> */}
          <Mascots3 scale={[8,8,8]} position={[0,-4,-2]}/>
          {/* <Cblock scale={[.01,.01,.01]} position={[0,0,-8]} /> */}
          <Hoop scale={[.1,.1,.1]} />
          {/* <Rig /> */}
        </Suspense>
      </Canvas>
    </>
  );
}