import React, { useRef, useEffect } from 'react'
import { useGLTF, useAnimations } from '@react-three/drei'
import { useStore } from '../hooks/useStore';
import { LoopOnce } from 'three';


export default function Mascots(props){
  const [blockingLeft, blockingRight] = useStore((state) => [
    state.blockingLeft,
    state.blockingRight,
  ]);
  return(
    <>
      {blockingLeft && <BothBlockL {...props}/>}
      {blockingRight && <BothBlockR {...props}/>}
      {!blockingLeft && !blockingRight && <BothIdle {...props}/>}
    </>
  )
}

function BothBlockL(props) {
    const group = useRef()
    const { nodes, materials, animations } = useGLTF('/both_blockLFJ.glb')
    const { actions } = useAnimations(animations, group)
  
    useEffect(() => {
      actions["All Animations"].setLoop(LoopOnce);
      void actions["All Animations"].play();
    }, [])
    return (
      <group ref={group} {...props} dispose={null}>
        <group>
          <group name="mavsman_idle" position={[0.34, 0.1, -0.53]}>
            <mesh
              name="mavsman_idle_legs"
              castShadow
              receiveShadow
              geometry={nodes.mavsman_idle_legs.geometry}
              material={nodes.mavsman_idle_legs.material}
              position={[0.02, -0.02, -0.03]}
            />
            <mesh
              name="mavsman_idle_fence"
              castShadow
              receiveShadow
              geometry={nodes.mavsman_idle_fence.geometry}
              material={nodes.mavsman_idle_fence.material}
              position={[-0.18, 0.36, 0.01]}
              rotation={[0, 0, -0.01]}
            />
            <mesh
              name="mavsman_idle_head"
              castShadow
              receiveShadow
              geometry={nodes.mavsman_idle_head.geometry}
              material={nodes.mavsman_idle_head.material}
              position={[-0.02, 0.42, 0]}
            />
            <mesh
              name="mavsman_idle_armR"
              castShadow
              receiveShadow
              geometry={nodes.mavsman_idle_armR.geometry}
              material={nodes.mavsman_idle_armR.material}
              position={[0.19, 0.38, 0.03]}
              rotation={[0, 0, -0.01]}
            />
            <mesh
              name="mavsman_idle_body"
              castShadow
              receiveShadow
              geometry={nodes.mavsman_idle_body.geometry}
              material={nodes.mavsman_idle_body.material}
              position={[0.02, 0.29, -0.02]}
            />
          </group>
          <group name="champ_block" position={[-0.24, 0.86, -0.56]}>
            <mesh
              name="champ_block_tail"
              castShadow
              receiveShadow
              geometry={nodes.champ_block_tail.geometry}
              material={nodes.champ_block_tail.material}
              position={[-0.02, -0.47, -0.03]}
              rotation={[0, 0, -Math.PI / 9]}
            />
            <mesh
              name="champ_block_armR"
              castShadow
              receiveShadow
              geometry={nodes.champ_block_armR.geometry}
              material={nodes.champ_block_armR.material}
              position={[0.11, -0.39, 0.2]}
              rotation={[0, 0, 0.01]}
            />
            <mesh
              name="champ_block_armL"
              castShadow
              receiveShadow
              geometry={nodes.champ_block_armL.geometry}
              material={nodes.champ_block_armL.material}
              position={[-0.01, -0.33, 0.01]}
              rotation={[0, 0, 0.01]}
            />
            <mesh
              name="champ_block_D"
              castShadow
              receiveShadow
              geometry={nodes.champ_block_D.geometry}
              material={nodes.champ_block_D.material}
              position={[0.17, -0.09, 0]}
              rotation={[0, 0, 0.01]}
            />
            <mesh
              name="champ_block_head"
              castShadow
              receiveShadow
              geometry={nodes.champ_block_head.geometry}
              material={nodes.champ_block_head.material}
              position={[0.04, -0.24, 0]}
              rotation={[0, 0, 0.21]}
            />
            <mesh
              name="champ_block_legL"
              castShadow
              receiveShadow
              geometry={nodes.champ_block_legL.geometry}
              material={nodes.champ_block_legL.material}
              position={[-0.16, -0.54, 0]}
            />
            <mesh
              name="champ_block_legR"
              castShadow
              receiveShadow
              geometry={nodes.champ_block_legR.geometry}
              material={nodes.champ_block_legR.material}
              position={[0.02, -0.67, 0]}
            />
            <mesh
              name="champ_block_body"
              castShadow
              receiveShadow
              geometry={nodes.champ_block_body.geometry}
              material={nodes.champ_block_body.material}
              position={[0.06, -0.47, 0]}
            />
          </group>
          <mesh
            name="champ_fx_hit"
            castShadow
            receiveShadow
            geometry={nodes.champ_fx_hit.geometry}
            material={materials.fx_hit}
            position={[-0.16, 1.26, -0.59]}
            rotation={[0, 0, -1.85]}
            scale={[0, 0, 0]}
          />
          <mesh
            name="champ_fx_bg"
            castShadow
            receiveShadow
            geometry={nodes.champ_fx_bg.geometry}
            material={materials.fx_bg}
            position={[-0.16, 1.26, -0.68]}
            scale={[0, 0, 0]}
          />
        </group>
      </group>
    )
  }

function BothIdle(props) {
  const group = useRef()
  const { nodes, materials, animations } = useGLTF('/both_idleFJ.glb')
  const { actions } = useAnimations(animations, group)

  useEffect(() => void actions["All Animations"].play(), [])
  return (
    <group ref={group} {...props} dispose={null}>
      <group>
        <group name="mavsman_idle" position={[0.34, 0.1, -0.53]}>
          <mesh
            name="mavsman_idle_legs"
            castShadow
            receiveShadow
            geometry={nodes.mavsman_idle_legs.geometry}
            material={nodes.mavsman_idle_legs.material}
            position={[0.02, -0.02, -0.03]}
          />
          <mesh
            name="mavsman_idle_fence"
            castShadow
            receiveShadow
            geometry={nodes.mavsman_idle_fence.geometry}
            material={nodes.mavsman_idle_fence.material}
            position={[-0.18, 0.36, 0.01]}
            rotation={[0, 0, -0.92]}
          />
          <mesh
            name="mavsman_idle_head"
            castShadow
            receiveShadow
            geometry={nodes.mavsman_idle_head.geometry}
            material={nodes.mavsman_idle_head.material}
            position={[-0.02, 0.41, 0]}
          />
          <mesh
            name="mavsman_idle_armR"
            castShadow
            receiveShadow
            geometry={nodes.mavsman_idle_armR.geometry}
            material={nodes.mavsman_idle_armR.material}
            position={[0.19, 0.38, 0.03]}
            rotation={[0, 0, -0.43]}
          />
          <mesh
            name="mavsman_idle_body"
            castShadow
            receiveShadow
            geometry={nodes.mavsman_idle_body.geometry}
            material={nodes.mavsman_idle_body.material}
            position={[0.02, 0.29, -0.02]}
          />
        </group>
        <group name="champ_idle" position={[-0.28, 0.08, -0.55]}>
          <mesh
            name="champ_idle_tail"
            castShadow
            receiveShadow
            geometry={nodes.champ_idle_tail.geometry}
            material={nodes.champ_idle_tail.material}
            position={[-0.06, 0.32, -0.03]}
            rotation={[0, 0, 0.24]}
          />
          <mesh
            name="champ_idle_arms"
            castShadow
            receiveShadow
            geometry={nodes.champ_idle_arms.geometry}
            material={nodes.champ_idle_arms.material}
            position={[0.05, 0.45, 0.02]}
            rotation={[0, 0, -0.02]}
          />
          <mesh
            name="champ_idle_head"
            castShadow
            receiveShadow
            geometry={nodes.champ_idle_head.geometry}
            material={nodes.champ_idle_head.material}
            position={[0.08, 0.47, 0.03]}
            rotation={[0, 0, -0.01]}
          />
          <mesh
            name="champ_idle_body"
            castShadow
            receiveShadow
            geometry={nodes.champ_idle_body.geometry}
            material={nodes.champ_idle_body.material}
            position={[-0.05, 0.01, 0]}
          />
        </group>
      </group>
    </group>
  )
}

