let ballNum = 0;
let ballActive = -1;
const ballIterations = 5;

function RapidBalls(){
  return new Array(ballIterations).fill().map((_, i) => {
    return <BallRapid key={i} toast={i} scale={[.1,.1,.1]} rotation={[0,Math.PI,0]}/>
  })
}

let limitingShots = true;
let shotBuffering = false;

export function Balls() {

  const handleBrowsRaised = (event) => {
      if(!shotBuffering){
        ballActive = ballNum;
        ballNum++;
        if(ballNum>=ballIterations) 
          ballNum = 0;
        shotBuffering=true;
        if(limitingShots){
          setTimeout(function() { shotBuffering=false }, 500);
        } else {
          setTimeout(function() { shotBuffering=false }, 75);
        }
      }

  };
  React.useEffect(() => {
    document.addEventListener('browsRaised', handleBrowsRaised);
    return () => {
      document.addEventListener('browsRaised', handleBrowsRaised);
    };
  }, []);
  return (
    <>
      <BallOutL toast={0} scale={[.1,.1,.1]} rotation={[0,Math.PI,0]} />
      <BallOutR toast={0} scale={[.1,.1,.1]} rotation={[0,Math.PI,0]} />
      <BallIn toast={1} scale={[.1,.1,.1]} rotation={[0,Math.PI,0]} />
      <RapidBalls />
    </>
  )
  // return new Array(ballIterations).fill().map((_, i) => {
  //   return <Ball key={i} toast={i} />
  // })
}